@import 'src/common/styles/sass/mixins';
@import 'src/common/styles/sass/variables';

.contactsBlock {
  background-image: url("../assets/image/contacts.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;

  .contactsContainer {
    flex-direction: column;

    .contactForm {
      @include flexBox(flex, column, center, null, null);
      width: 50%;

      .input {
        margin-bottom: 30px;
        border-radius: 22px;
        height: 30px;
        padding: 5px 25px;
        border: 1px solid #d9d9d9;
        background-color: #ffffff;
        width: 100%;
      }

      .textarea {
        min-height: 90px;
      }

      @media screen and (max-width: 394px) {
        width: 100%;
      }

      button {
        @include text($titleFontFamily, 14px, 22px, white, 700);
        letter-spacing: 1.5px;
        text-transform: uppercase;
        padding: 0 45px;
        background-color: $primaryColor;
        border-radius: 29px;
        border: 3px solid $primaryColor;
        height: 60px;
        width: 250px;
        margin-right: 15px;
        position: relative;

        &:hover {
          background-color: white;
          color: $primaryColor;
          border: 3px solid $primaryColor;
          border-radius: 29px;
          cursor: pointer;
        }
        &:disabled {
          background-color: #5f6366;
          color: #c1c7cc;
          border: 3px solid #c1c7cc;
          cursor: wait;
        }
      }


    }

  }
  button {
    @include text($titleFontFamily, 14px, 22px, white, 700);
    letter-spacing: 1.5px;
    text-transform: uppercase;
    background-color: $primaryColor;
    border-radius: 29px;
    border: 3px solid $primaryColor;
    height: 40px;
    width: 70px;
    position: relative;
    margin: 15px;

    &:hover {
      background-color: white;
      color: $primaryColor;
      border: 3px solid $primaryColor;
      border-radius: 29px;
      cursor: pointer;
    }
    &:disabled {
      background-color: #5f6366;
      color: #c1c7cc;
      border: 3px solid #c1c7cc;
      cursor: wait;
    }
  }
}

.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
}






